import { DrawLayerData } from "./DrawLayerData";
export class PixcelArtData {
    //=============================================
    // constructor
    //=============================================
    constructor(isBlank = false) {
        //=============================================
        // private
        //=============================================
        //=============================================
        // public
        //=============================================
        this.setValue = (dotJsonObj, id = null, title = null) => {
            this._id = id;
            this._title = title;
            this._drawLayerDataList = {};
            if (dotJsonObj != null) {
                this._dotJsonObj = dotJsonObj;
                for (var key in this._dotJsonObj) {
                    let layerJsonObj = this._dotJsonObj[key];
                    let dld = new DrawLayerData();
                    //console.log(key, layerJson);
                    dld.setJsonObj(layerJsonObj);
                    this._drawLayerDataList[key] = dld;
                }
            }
        };
        this.getJsonObj = () => {
            let obj = {};
            for (var key in this._drawLayerDataList) {
                let layerData = this._drawLayerDataList[key];
                obj[key] = layerData.getJsonObj();
            }
            let result = { "id": this._id, "title": this._title, "dot_json": obj };
            //console.log('\n[JsonObj]', "\n\t", result);
            //console.log('\n[JsonStr]', "\n\t", JSON.stringify(result));
            return result;
        };
        //色配列を返す
        this.getHexColorCodeList = () => {
            let result = [];
            for (var key in this._drawLayerDataList) {
                let dld = this._drawLayerDataList[key];
                let hexColorCodeList = dld.hexColorCodeList;
                result = result.concat(hexColorCodeList);
            }
            //重複削除
            result = Array.from(new Set(result));
            //最初の１つを削除
            result.shift();
            return result;
        };
        this.addDrawLayerData = (name, layerData) => {
            //console.log("setLayerData", name, layerData);
            this._drawLayerDataList[name] = layerData;
            //this.layoutDataInit();
        };
        this.getDrawLayerDataList = () => {
            return this._drawLayerDataList;
        };
        this.layoutInit = () => {
            //console.log(this._dotJsonObj);
            for (var key in this._dotJsonObj) {
                let dot_json = this._dotJsonObj[key];
                let layerData = new DrawLayerData();
                layerData.setJsonObj(dot_json);
                this._drawLayerDataList[key] = layerData;
            }
            let minX = Number.MAX_SAFE_INTEGER;
            let minY = Number.MAX_SAFE_INTEGER;
            let maxX = 0;
            let maxY = 0;
            //console.log(this._drawLayerDataList);
            for (var key in this._drawLayerDataList) {
                let ld = this._drawLayerDataList[key];
                if (ld.x < minX) {
                    minX = ld.x;
                }
                if (ld.y < minY) {
                    minY = ld.y;
                }
                if (maxX < ld.x + ld.width) {
                    maxX = ld.x + ld.width;
                }
                if (maxY < ld.y + ld.height) {
                    maxY = ld.y + ld.height;
                }
            }
            this._x = minX;
            this._y = minY;
            this._width = maxX - minX;
            this._height = maxY - minY;
        };
        this._drawLayerDataList = {};
        if (isBlank) {
            this.addDrawLayerData("body", new DrawLayerData(true));
        }
    }
    //=============================================
    // getter/setter
    //=============================================
    get id() {
        return this._id;
    }
    get title() {
        return this._title;
    }
    get drawLayerDataList() {
        return this._drawLayerDataList;
    }
    get x() {
        return this._x;
    }
    set x(value) {
        this._x = value;
    }
    get y() {
        return this._y;
    }
    set y(value) {
        this._y = value;
    }
    get width() {
        return this._width;
    }
    get height() {
        return this._height;
    }
}
