import { State } from "../../../model/State";
import { PixcelArtData } from "../../../model/data/PixcelArtData";
import { BgLayer } from "./layer/BgLayer";
import { CursorLayer } from "./layer/CursorLayer";
import { DrawLayer } from "./layer/DrawLayer";
export class Workspace extends createjs.Stage {
    //----------protected-------
    //=============================================
    // constructor
    //=============================================
    constructor(state, canvasId) {
        super(canvasId);
        //=============================================
        // TODO
        //=============================================
        /*
            マウスを素早く動かしたときに処理が追いつかないときがある。
             mousemoveよりsetIntervalにしたほうがいいかも？
        */
        //=============================================
        // 定数/変数
        //=============================================
        //----------public----------
        this.EVENT_CHANGE_WS = "event change workspace";
        this.EVENT_EXTRACT_HEX_COLOR = "event extract hex color";
        //=============================================
        // event handler
        //=============================================
        //=============================================
        // private
        //=============================================
        this._resetStageSize = () => {
            let canvas = this.canvas;
            let stageW = canvas.width;
            let stageH = canvas.height;
            let remainderX = (stageW - this._stageMargin * 2) % this._dotSize;
            let remainderY = (stageH - this._stageMargin * 2) % this._dotSize;
            let left = Math.floor(remainderX / 2) + this._stageMargin;
            let right = stageW - (remainderX - (left - this._stageMargin)) - this._stageMargin;
            let top = Math.floor(remainderY / 2) + this._stageMargin;
            let bottom = stageH - (remainderY - (top - this._stageMargin)) - this._stageMargin;
            this._bgLayer.setStageSize(stageW, stageH, this._dotSize, left, top, right, bottom);
            for (let drawLayer of this._drawLayerList) {
                drawLayer.setStageSize(stageW, stageH, this._dotSize, left, top, right, bottom);
            }
            this._cursroLayer.setStageSize(stageW, stageH, this._dotSize, left, top, right, bottom);
            this._drawAreaTop = top;
            this._drawAreaRight = right;
            this._drawAreaBottom = bottom;
            this._drawAreaLeft = left;
            this.update();
        };
        this._addDrawLayer = (layerName) => {
            let dl;
            let isExist = false;
            for (var i = 0; i < this._drawLayerList.length; i++) {
                dl = this._drawLayerList[i];
                if (layerName == dl.name) {
                    isExist = true;
                    break;
                }
            }
            if (isExist) {
                //alert("このレイヤーはすでに登録されています");
                throw new Error("[ERROR:add DrawLayer]\n\t" + "name\t:\t" + layerName);
            }
            else {
                //console.log("\t", "add draw layer\t:", layerName);
                dl = new DrawLayer(this._state, layerName);
                let len = this._drawLayerList.push(dl);
                this._activeDrawLayerId = len - 1;
                this.addChild(dl);
            }
        };
        this._addMouseEventListener = () => {
            // インタラクティブの設定
            //TODO　描画のあるところでないとイベントが発生しない？
            this.addEventListener("stagemousedown", (e) => {
                //エリア外なら処理をしない
                if (!this._areaHitTest(this.mouseX, this.mouseY)) {
                    return false;
                }
                //お絵かきモードでないなら処理しない
                //if(!this.isAbleDraw){return false;}
                if (this._state.currentCategory != State.CATEGORY_DRAW) {
                    return false;
                }
                this._isMouseDown = true;
                let layer = this._getActiveDrawLayer();
                if (layer) {
                    layer.drawDot(this.mouseX, this.mouseY, this._hexColor);
                    this.update();
                }
            });
            this.addEventListener("stagemouseup", (e) => {
                //エリア外なら処理をしない
                if (!this._areaHitTest(this.mouseX, this.mouseY)) {
                    return false;
                }
                if (this._state.current == State.EDIT_DROPPER) {
                    let layer = this._getActiveDrawLayer();
                    if (layer) {
                        let hexColor = layer.getDotHexColor(this.mouseX, this.mouseY);
                        //console.log(hexColor);
                        if (hexColor && this._hexColor != hexColor) {
                            this._hexColor = hexColor;
                            this.dispatchEvent(new createjs.Event(this.EVENT_EXTRACT_HEX_COLOR, true, true));
                        }
                    }
                }
                //お絵かきモードでないなら処理しない
                //if(!this.isAbleDraw){return false;}
                if (this._state.currentCategory != State.CATEGORY_DRAW) {
                    return false;
                }
                if (this._isMouseDown) {
                    if (this._state.current == State.DRAW_PENCIL || this._state.current == State.DRAW_ERACER) {
                        this._saveHistory();
                        this.dispatchEvent(new createjs.Event(this.EVENT_CHANGE_WS, true, true));
                    }
                }
                this._isMouseDown = false;
            });
            this.addEventListener("stagemousemove", (e) => {
                //エリア外なら処理をしない
                if (!this._areaHitTest(this.mouseX, this.mouseY)) {
                    return false;
                }
                this._cursroLayer.move(this.mouseX, this.mouseY);
                //お絵かきモードでないなら処理しない
                //if(!this.isAbleDraw){return false;}
                if (this._state.currentCategory == State.CATEGORY_DRAW) {
                    if (this._isMouseDown) {
                        let layer = this._getActiveDrawLayer();
                        if (layer) {
                            layer.drawDot(this.mouseX, this.mouseY, this._hexColor);
                        }
                    }
                }
                this.update();
            });
        };
        this._getActiveDrawLayer = () => {
            if (this._drawLayerList.length < 1) {
                return null;
            }
            return this._drawLayerList[this._activeDrawLayerId];
        };
        this._areaHitTest = (mx, my) => {
            if (this._drawAreaLeft < mx && mx < this._drawAreaRight && this._drawAreaTop < my && my < this._drawAreaBottom) {
                return true;
            }
            else {
                return false;
            }
        };
        this._saveHistory = () => {
            //ログを１０件までに処理
            if (10 <= this._pixcelArtDataHistoryList.length) {
                this._pixcelArtDataHistoryList.shift();
            }
            //ヒストリーの途中で、ドローやファイル読み込みを行った場合に以降のログを削除する
            if (this._histroyId < this._pixcelArtDataHistoryList.length - 1) {
                this._pixcelArtDataHistoryList.splice(this._histroyId + 1, this._pixcelArtDataHistoryList.length - 1);
            }
            var pad = this.getPixcelArtData();
            this._pixcelArtDataHistoryList.push(pad);
            this._histroyId = this._pixcelArtDataHistoryList.length - 1;
            console.log("history", "save", this._histroyId);
            //TODO　ログリストとログIDの値からUNDO/REDOボタンの有効・無効を切り替えられるようにしたい
        };
        /*
        private _getHexColorCode = (mx:number, my:number) =>{
            let result:string;
            let xx:number = Math.floor((mx - this._drawAreaLeft) / this._dotSize);
            let yy:number = Math.floor((my - this._drawAreaTop) / this._dotSize);
            let padJsonObj :any = this._pad.getJsonObj();
            let dl:DrawLayer = this._getActiveDrawLayer();
            let dlName:string = dl.name;
            let layerJsonObj :any = padJsonObj.dot_json[dlName];
            let minX:number = 0;
            let minY:number = 0;
            if(layerJsonObj.pos){
                minX = layerJsonObj.pos[0];
                minY = layerJsonObj.pos[1];
            }
            let w:number = layerJsonObj.size[0];
            let h:number = layerJsonObj.size[1];
            let maxX:number = minX + w - 1;
            let maxY:number = minY + h - 1;
            if(minX <= xx && xx <= maxX && minY <= yy && yy <= maxY){
                let dx :number = xx-minX;
                let dy :number = yy-minY;
                let count :number = dx + dy * h;
                let hexIndex = layerJsonObj.data[count];
                result = layerJsonObj.color[hexIndex];
                if(result){
                    this._extractHexColor = result;
                    this.dispatchEvent(new createjs.Event(this.EVENT_GET_HEX_COLOR_CODE, true, true));
                }
            }
        }
        */
        //=============================================
        // public
        //=============================================
        this.setPixcelArtData = (pad, isSaveLog = true) => {
            //console.log("reset", "workspace");
            //this._pad = pad;
            this.removeAllChildren();
            this._drawLayerList = [];
            //背景レイヤーをAddChild
            this.addChild(this._bgLayer);
            //this.update();
            if (pad.id) {
                //this._wpId = pad.id;
            }
            //this._wpTitle = data.title;
            let drawLayerDataList = pad.drawLayerDataList;
            //レイヤーの追加
            for (var key in drawLayerDataList) {
                this._addDrawLayer(key);
            }
            //カーソルレイヤーをAddChild
            this.addChild(this._cursroLayer);
            //サイズ指定
            this._resetStageSize();
            //データセット
            let dl;
            let dld;
            for (var i = 0; i < this._drawLayerList.length; i++) {
                dl = this._drawLayerList[i];
                dld = drawLayerDataList[dl.name];
                dl.setDrawLayerData(dld);
            }
            this.update();
            if (isSaveLog) {
                this._saveHistory();
            }
            this.dispatchEvent(new createjs.Event(this.EVENT_CHANGE_WS, true, true));
        };
        this.getPixcelArtData = () => {
            let result = new PixcelArtData();
            let layer;
            let layerData;
            for (let i = 0; i < this._drawLayerList.length; i++) {
                layer = this._drawLayerList[i];
                layerData = layer.getDrawLayerData();
                //if(layerData){
                result.addDrawLayerData(layer.name, layerData);
                //}
            }
            result.layoutInit();
            //this._pad = result;
            return result;
        };
        this.undo = () => {
            if (0 < this._histroyId) {
                this._histroyId--;
                console.log("history", "undo", this._histroyId);
                let pad = this._pixcelArtDataHistoryList[this._histroyId]; //TODO リストにはPixcelArtDataをいれるようにする
                //let data: PixcelArtData = new PixcelArtData()
                //data.setJson(jsonObj);
                this.setPixcelArtData(pad, false);
                //this.dispatchEvent(new createjs.Event(this.EVENT_CHANGE_WS, true, true));
            }
            else {
                console.log("これ以上古いログはありません");
            }
            //let layer: Layer = this._getActiveLayer();
            //layer.loadLog(0);
            //this.update();
        };
        this.redo = () => {
            if (this._histroyId < this._pixcelArtDataHistoryList.length - 1) {
                this._histroyId++;
                //this._reset();
                console.log("history", "redo", this._histroyId);
                let pad = this._pixcelArtDataHistoryList[this._histroyId];
                this.setPixcelArtData(pad, false);
                //this.dispatchEvent(new createjs.Event(this.EVENT_CHANGE_WS, true, true));
            }
            else {
                console.log("これ以上新しいログはありません");
            }
        };
        this._dotSize = 16;
        this._stageMargin = 2;
        this._state = state;
        // タッチ操作をサポートしているブラウザーなら、タッチ操作を有効にする
        if (createjs.Touch.isSupported() == true) {
            createjs.Touch.enable(this);
        }
        this._pixcelArtDataHistoryList = [];
        this._drawLayerList = [];
        this._bgLayer = new BgLayer(state);
        this._cursroLayer = new CursorLayer(state);
        this._addMouseEventListener();
        //this.isAbleDraw = true;
    }
    //=============================================
    // getter/setter
    //=============================================
    get hasPrevLog() {
        return (0 < this._histroyId);
    }
    get hasNextLog() {
        return (this._histroyId < this._pixcelArtDataHistoryList.length - 1);
    }
    get hexColor() {
        return this._hexColor;
    }
    set hexColor(value) {
        this._hexColor = value;
    }
}
