export class DrawLayerData {
    //=============================================
    // constructor
    //=============================================
    constructor(isBlank = false) {
        //=============================================
        // private
        //=============================================
        //=============================================
        // public
        //=============================================
        this.setData = (width, height, hexColorCodeList, dataList, x = 0, y = 0) => {
            this._width = width;
            this._height = height;
            this._hexColorCodeList = hexColorCodeList;
            this._dataList = dataList;
            this._x = x;
            this._y = y;
        };
        this.setJsonObj = (obj) => {
            let size = obj.size;
            this._width = size[0];
            this._height = size[1];
            this._hexColorCodeList = obj.color;
            this._dataList = obj.data;
            if (obj.pos) {
                let pos = obj.pos;
                this._x = pos[0];
                this._y = pos[1];
            }
            else {
                this._x = 0;
                this._y = 0;
            }
        };
        this.getJsonObj = () => {
            let result = { "size": [this._width, this._height], "color": this._hexColorCodeList, "data": this._dataList };
            if (this._x != 0 || this._y != 0) {
                result["pos"] = [this._x, this._y];
            }
            return result;
        };
        if (isBlank) {
            this._hexColorCodeList = ["", "333333"];
        }
        else {
            this._hexColorCodeList = [];
        }
        this._dataList = [];
    }
    //=============================================
    // getter/setter
    //=============================================
    /*
    get name(): string {
        return this._name;
    }
    set name(value: string) {
        this._name = value;
    }
    */
    get x() {
        return this._x;
    }
    set x(value) {
        this._x = value;
    }
    get y() {
        return this._y;
    }
    set y(value) {
        this._y = value;
    }
    get width() {
        return this._width;
    }
    get height() {
        return this._height;
    }
    get hexColorCodeList() {
        return this._hexColorCodeList;
    }
    get dataList() {
        return this._dataList;
    }
}
