import { PixcelArtData } from "../../model/data/PixcelArtData";
export class LocalStorageConnector {
    //----------protected-------
    //=============================================
    // constructor
    //=============================================
    constructor() {
        //=============================================
        // TODO
        //=============================================
        /*
        */
        //=============================================
        // 定数/変数
        //=============================================
        //----------public----------
        //----------private---------
        this._KEY = "dotanimal_doteditor";
        //=============================================
        // event handler
        //=============================================
        //=============================================
        // private
        //=============================================
        //=============================================
        // public
        //=============================================
        this.save = (pad) => {
            let jsonObj = pad.getJsonObj();
            let jsonStr = JSON.stringify(jsonObj);
            window.localStorage.setItem(this._KEY, jsonStr);
        };
        this.load = () => {
            let localStorageData = localStorage.getItem(this._KEY);
            if (localStorageData) {
                let localStorageDataObj = JSON.parse(localStorageData);
                if (localStorageDataObj.dot_json) {
                    let pad = new PixcelArtData();
                    pad.setValue(localStorageDataObj.dot_json);
                    pad.layoutInit();
                    if (0 < pad.width && 0 < pad.height) {
                        return pad;
                    }
                }
            }
            return null;
        };
    }
}
