//=============================================
// 定数/変数
//=============================================
//----------public----------
State.DRAW_PENCIL = 'draw pencil';
State.DRAW_ERACER = 'draw eracer';
State.EDIT_DROPPER = 'edit dropper';
State.SELECT_RANGE = 'select range';
State.HISTORY_UNDO = 'history undo';
State.HISTORY_REDO = 'history redo';
State.FILE_NEW = 'file new';
State.FILE_LOAD_JSON_FROM_LOCAL = 'file load json from local';
State.FILE_SAVE_JSON_TO_LOCAL = 'file save json to local';
State.FILE_SAVE_SVG_TO_LOCAL = 'file save svg to local';
State.CATEGORY_DRAW = 'category draw';
State.CATEGORY_EDIT = 'category edit';
State.CATEGORY_SELECT = 'category select';
State.CATEGORY_HISTORY = 'category histroy';
State.CATEGORY_FILE = 'category file';
export { State };
