import { Canvas2DrawLayerData } from "../../../../model/Canvas2DrawLayerData";
import { State } from "../../../../model/State";
import { Layer } from "./Layer";
export class DrawLayer extends Layer {
    //----------protected-------
    //=============================================
    // constructor
    //=============================================
    constructor(state, name) {
        super(state);
        //=============================================
        // event handler
        //=============================================
        //=============================================
        // private
        //=============================================
        /*
        private _pencil = (mx: number, my: number, color: string) => {
            this.graphics.clear();
            let xx = Math.floor((mx - this._drawAreaLeft) / this._dotSize) * this._dotSize + this._drawAreaLeft;
            let yy = Math.floor((my - this._drawAreaTop) / this._dotSize) * this._dotSize + this._drawAreaTop;
            this.graphics.beginFill(color);
            this.graphics.drawRect(xx, yy, this._dotSize, this._dotSize);
            //描画した順に上書き（追記）
            this.updateCache("source-over");
        }
        private _eraser = (mx: number, my: number) => {
            this.graphics.clear();
            let xx :number = Math.floor((mx - this._drawAreaLeft) / this._dotSize) * this._dotSize + this._drawAreaLeft;
            let yy :number = Math.floor((my - this._drawAreaTop) / this._dotSize) * this._dotSize + this._drawAreaTop;
            this.graphics.beginFill('#' + "000000");
            this.graphics.drawRect(xx, yy, this._dotSize, this._dotSize);
            //元からある図形に対して重なっていない部分のみ描画（消しゴム）
            this.updateCache("destination-out");
        }
        */
        //=============================================
        // public
        //=============================================
        this.setStageSize = (stageWidth, stageHeight, dotSize, drawAreaLeft, drawAreaTop, drawAreaRight, drawAreaBottom) => {
            this._superSetStageSize(stageWidth, stageHeight, dotSize, drawAreaLeft, drawAreaTop, drawAreaRight, drawAreaBottom);
            //console.log("[DrawLayer] change size");
        };
        this.drawDot = (mx, my, hexColor) => {
            this.graphics.clear();
            let xx = Math.floor((mx - this._drawAreaLeft) / this._dotSize) * this._dotSize + this._drawAreaLeft;
            let yy = Math.floor((my - this._drawAreaTop) / this._dotSize) * this._dotSize + this._drawAreaTop;
            this.graphics.beginFill('#' + hexColor);
            this.graphics.drawRect(xx, yy, this._dotSize, this._dotSize);
            if (this._state.current == State.DRAW_PENCIL) {
                //描画した順に上書き（追記）
                this.updateCache("source-over");
            }
            else if (this._state.current == State.DRAW_ERACER) {
                //元からある図形に対して重なっていない部分のみ描画（消しゴム）
                this.updateCache("destination-out");
            }
        };
        this.getDotHexColor = (mx, my) => {
            let dld = this.getRangeDrawLayerData(mx, my, mx + this._dotSize, my + this._dotSize);
            let jsonObj = dld.getJsonObj();
            if (jsonObj.color) {
                if (1 < jsonObj.color.length) {
                    return jsonObj.color[1];
                }
            }
        };
        this.getRangeDrawLayerData = (targetAreaLeft, targetAreaTop, targetAreaRight, targetAreaBottom) => {
            let cc = this.cacheCanvas;
            let ctx = cc.getContext("2d");
            let left = targetAreaLeft - (targetAreaLeft - this._drawAreaLeft) % this._dotSize;
            let top = targetAreaTop - (targetAreaTop - this._drawAreaTop) % this._dotSize;
            let right = targetAreaRight - (targetAreaRight - this._drawAreaRight) % this._dotSize;
            let bottom = targetAreaBottom - (targetAreaBottom - this._drawAreaBottom) % this._dotSize;
            let c2ld = new Canvas2DrawLayerData(ctx, this._stageWidth, this._stageHeight, this._dotSize, false, false, left, top, right, bottom);
            let dld = c2ld.getDrawLayerData(1);
            //let jsonObj:any = dld.getJsonObj();
            return dld;
        };
        /*
        public setHexColorCode = (value : string) => {
            this._hexColorCode =  value;
        }
        */
        this.setDrawLayerData = (dld) => {
            let xCount = dld.width;
            let yCount = dld.height;
            let baseX = dld.x;
            let baseY = dld.y;
            let hexColorCodeList = dld.hexColorCodeList;
            let dataList = dld.dataList;
            this.graphics.clear();
            for (var i = 0; i < dataList.length; i++) {
                let hexColorCode = '';
                let colorId = dataList[i];
                if (0 < colorId) {
                    if (Number.isInteger(colorId) == true) {
                        let xx = i % xCount + baseX;
                        let yy = Math.floor(i / xCount) + baseY;
                        hexColorCode = hexColorCodeList[colorId];
                        this.graphics.beginFill('#' + hexColorCode);
                        this.graphics.drawRect(xx * this._dotSize + this._drawAreaLeft, yy * this._dotSize + this._drawAreaTop, this._dotSize, this._dotSize);
                    }
                }
            }
            this.updateCache("source-over");
        };
        this.getDrawLayerData = () => {
            let cc = this.cacheCanvas;
            let ctx = cc.getContext("2d");
            let c2ld = new Canvas2DrawLayerData(ctx, this._stageWidth, this._stageHeight, this._dotSize, false, true, this._drawAreaLeft, this._drawAreaTop, this._drawAreaRight, this._drawAreaBottom);
            return c2ld.getDrawLayerData(1);
        };
        this.name = name;
    }
}
