export class Layer extends createjs.Shape {
    //=============================================
    // constructor
    //=============================================
    constructor(state) {
        super();
        //=============================================
        // event handler
        //=============================================
        //=============================================
        // private
        //=============================================
        //=============================================
        // public
        //=============================================
        this.setStageSize = (stageWidth, stageHeight, dotSize, drawAreaLeft, drawAreaTop, drawAreaRight, drawAreaBottom) => {
        };
        //=============================================
        // protected
        //=============================================
        this._superSetStageSize = (stageWidth, stageHeight, dotSize, drawAreaLeft, drawAreaTop, drawAreaRight, drawAreaBottom) => {
            this._stageWidth = stageWidth;
            this._stageHeight = stageHeight;
            this._dotSize = dotSize;
            this._drawAreaLeft = drawAreaLeft;
            this._drawAreaTop = drawAreaTop;
            this._drawAreaRight = drawAreaRight;
            this._drawAreaBottom = drawAreaBottom;
            this.cache(0, 0, this._stageWidth, this._stageHeight);
        };
        this._state = state;
    }
}
